<template>
  <div>
    <el-breadcrumb separator-icon="ArrowRight" style="margin: 16px">
      <el-breadcrumb-item :to="{ path: '/home' }">首页</el-breadcrumb-item>
      <el-breadcrumb-item>电费管理</el-breadcrumb-item>
    </el-breadcrumb>
    <el-card style="margin: 15px; min-height: calc(100vh - 111px)">
      <div>
        <!--    功能区-->
        <div style="margin: 10px 0">
          <!--    搜索区-->
          <div style="margin: 10px 0">
            <el-date-picker
              v-model="dateRange"
              type="datetimerange"
              range-separator="至"
              start-placeholder="开始日期"
              end-placeholder="结束日期">
            </el-date-picker>
            <!-- <el-input v-model="search" clearable placeholder="请输入标题" prefix-icon="Search" style="width: 20%"/> -->
            <el-button icon="Search" style="margin-left: 5px" type="primary" @click="load"></el-button>
            <!-- <el-button icon="refresh-left" style="margin-left: 10px" type="default" @click="reset"></el-button> -->



            <div style="float: right">
              <el-tooltip content="添加" placement="top">
                <el-button icon="plus" v-if="this.username != 'admin'" style="width: 100px" type="primary" @click="add"
                >电费充值</el-button>
                <el-button icon="plus" v-if="this.username == 'admin'" style="width: 100px" type="primary" @click="add"
                >电费扣除</el-button>
              </el-tooltip>
            </div>
          </div>
        </div>
        <!--    表格-->
        <el-table v-loading="loading" :data="tableData" border max-height="705" style="width: 100%">
          <el-table-column label="#" type="index"/>
          <el-table-column label="房间号" prop="roomid" sortable width="200px"/>
          <el-table-column label="操作人" prop="operatorName" width="200px"/>
          <el-table-column label="充值/扣费" prop="type" width="200px"><template #default="scope">
              <el-tag
                  :type="scope.row.type == '充值' ? 'success' : 'error'"
                  disable-transitions
              >{{ scope.row.type }}
              </el-tag>
            </template></el-table-column>>
          <el-table-column label="金额" prop="money" width="200px"/>
          <el-table-column label="时间" prop="date" sortable/>
        </el-table>
        <!--分页-->
        <div style="margin: 10px 0">
          <el-pagination
              v-model:currentPage="currentPage"
              :page-size="pageSize"
              :page-sizes="[10, 20]"
              :total="total"
              layout="total, sizes, prev, pager, next, jumper"
              @size-change="handleSizeChange"
              @current-change="handleCurrentChange"
          >
          </el-pagination>
        </div>
        <!--      弹窗-->
        <div>
          <el-dialog v-model="dialogVisible" title="电费" width="30%" @close="cancel">
            <el-form ref="form" :model="form" :rules="rules" label-width="120px" v-if="this.username == 'admin'">
              <el-form-item label="房间号" prop="roomid" style="margin-bottom: 27px">
                <el-input v-model="form.roomid"  style="width: 50%"
                ></el-input>
              </el-form-item>
              <el-form-item label="单价(元)" prop="" style="margin-bottom: 27px">
                <el-input v-model="this.defaultPrice"  style="width: 50%"
                ></el-input>
              </el-form-item>
              <el-form-item label="电费度数" prop="money" v-if="this.username == 'admin'">
                <el-input v-model="form.money" clearable style="width: 50%"></el-input>
              </el-form-item>
            </el-form>

            <el-form ref="form" :model="form" :rules="rules" label-width="120px" v-if="this.username != 'admin'">
              <el-form-item label="充值金额" prop="money">
                <el-input v-model="form.money" clearable style="width: 50%"></el-input>
              </el-form-item>
            </el-form>
            <template #footer>
              <span class="dialog-footer">
                <el-button @click="cancel">取 消</el-button>
             
                  <el-button v-if="this.username == 'admin'" type="primary" @click="save">确定扣费</el-button>
                  <el-button  v-if="this.username != 'admin'" type="primary" @click="save">确定支付</el-button>
     
                
              </span>
            </template>
          </el-dialog>
        </div>
      </div>
    </el-card>
  </div>
</template>
<script src="@/assets/js/ElectricManager.js"></script>